import * as actionTypes from "../actions/actionTypes";
import {
  updateState,
  processSentencesJSONfromGoogleSpreadsheet as processJSON
} from "../../utilities/utilities";

const initialState = {
  fetchedData: null,
  loading: false,
  error: null,
};

const fetchSuccessAux = (state, action) => {
  console.log('action data:', processJSON(action.data));
  return updateState(state, {
    loading: false,
    fetchedData: processJSON(action.data)
  });
};

const fetchFailAux = (state, action) => {
  return updateState(state, { loading: false, error: action.error });
};

const fetchStartAux = (state, action) => {
  return updateState(state, { loading: true });
};

const sentencesConstructorReducer = (state = initialState, action) => {
  console.log(state, action);
  switch (action.type) {
    case actionTypes.FETCH_SUCCESS:
      return fetchSuccessAux(state, action);
    case actionTypes.FETCH_FAIL:
      return fetchFailAux(state, action);
    case actionTypes.FETCH_START:
      return fetchStartAux(state, action);
    default:
      return state;
  }
  return state;
};

export default sentencesConstructorReducer;



