export const shakeArray = arr => {
  return arr.sort(() => Math.random() - 0.5);
};

export const updateState = (oldState, updates) => {
  return {
    ...oldState,
    ...updates
  };
};

export const processSentencesJSONfromGoogleSpreadsheet = (data) => {
  data = data.feed.entry;
  const arr = [];

  data.forEach((el, ind) => {
    arr.push({
      correctSentence: el.gsx$correctsentence.$t.split('+'),
      translation: el.gsx$translation.$t,
      wordSets: el.gsx$wordsets.$t.split('|').map((el) => el.split('+')),
      taskName: el.gsx$taskname.$t,
    });
  });

  return arr;
};

export const processJSONfromGoogleSpreadsheet = data => {
  data = data.feed.entry;
  let arr = [];
  data.forEach((elem, index) => {
    arr.push({});
    let reg = /gsx\$/;
    for (let key in elem) {
      if (~key.search(reg)) {
        arr[index][key.slice(4).toLowerCase()] = elem[key].$t; //'gsx$'.length = 4
      }
    }
    arr[index].shakenArray = shakeArray(
      elem.gsx$word.$t.split("")
    );
  });

  return arr;
};

export const isMobile = () => {
  let devicesList = navigator.userAgent||navigator.vendor||window.opera;
  let expr = /(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i;
  return expr.test(devicesList);
};

export const voiceOver = (text, options = {}) => {
  const {
    lang = 'en',
    rate = .8,
  } = options;

  if (!("speechSynthesis" in window)) return;
  const speech = new SpeechSynthesisUtterance();
  const speak = window.speechSynthesis;
  speech.lang = lang;
  speech.rate = rate;
  speech.text = text;
  speak.speak(speech);
};


